body {
  box-sizing: border-box;
  background-color: rgb(240, 144, 76);
}

.top {
  background-color: chocolate;
  border-radius: 10px;
  padding-bottom: 10px;
}

.flex {
  display:flex;
  align-items: center;
  justify-content: center;
}

h3 {
  ;
}

input {
  padding: 10px 60px;
  display: flex;
  justify-content: center;
  margin: 20px auto;
  border-radius: 10px;
  transition: all .3s ease-in-out;
}

input:hover {
  transform: scale(1.2);
  border: 1px solid greenyellow;
}

.addTask {
  padding: 10px;
  margin: 20px auto;
  margin-bottom: 10px;
  display: flex;
  border-radius: 10px;
  border: none;
  background-color: greenyellow;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

button:hover {
  color: black;
  background-color: rgb(240, 144, 76);
}


h1 {
  text-align: center;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 10px;
}

.delete, .completed {
  display: block;
  border-radius: 10px;
  border: none;
  background-color: greenyellow;
  cursor: pointer;
  transition: all .3s ease-in-out;
  padding: 10px;
  margin-left: 10px;
}



